<template>
  <div class="about">
    <button @click="logout" class="btn black">Logout</button>
    <ul class="collection">
      <li class="collection-item" v-for="details in info">
        {{ details.platform }} - {{ details.email }} - {{ details.password }}
      </li>
    </ul>
  </div>
</template>

<script>
  import db from "../components/firebaseInit";
  import firebase from "firebase/app";
  import "firebase/auth";
  export default {
    name: "about",
    data() {
      return {
        info: []
      };
    },
    created() {
      db.collection("login")
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              platform: doc.data().platform,
              password: doc.data().password,
              email: doc.data().email
            };
            this.info.push(data);
          });
        });
    },
    methods: {
      logout() {
        firebase
          .auth()
          .signOut()
          .then(() => {
            alert("Signout Successful");
            this.$router.go({ path: this.$router.path });
          })
          .catch(function(error) {
            alert(error.message);
          });
      }
    }
  };
</script>
